
















































































































































































































 /*头*/
header{
     position: relative;
     width: 100%;
     height:1rem;
     background: #FFD9B0;
     display: -webkit-box;
     display: -ms-flexbox;
     display: flex;
}
.arrow{
     position: absolute;
     width: 0.87rem;
     height: 0.87rem;
     -webkit-box-orient: horizontal;
     -webkit-box-direction: normal;
         -ms-flex-direction: row;
             flex-direction: row;
}
.arrow_left{
     font-size: 0.55rem;
     font-family: "宋体";
     font-weight: 800;
     text-align: center;
     line-height: 1rem;
}
.arrow_left>a{
     color: #fff;
}
.shoptwoinput{
    position: absolute;
    width: 85%;
    height: 65%;
    border-radius: .4rem;
    background-color: #f8f8f8;
    padding: .173333rem .4rem;
    font-size: .3rem;
    color: #666;
    outline: none;
    border: none;
    font-family: inherit;
    line-height: 0.89rem;
    right: 0.2rem;
    bottom:0.17rem;
}
.filter{
     position: relative;
     border-bottom: 1px solid #ddd;
     height: 0.8rem;
     line-height: 0.8rem;
     z-index: 100;
}
.filter_nav{
     display: block;
     width: 33.33%;
     height: 100%;
     -webkit-box-flex: 1;
         -ms-flex: 1;
             flex: 1;
     text-align: center;
     color: #666;
     float: left;
     font-size: .3rem;
     position: relative;
}
.active{
     /*color: #0092FF;*/
}
.sanjiao{
     width: 0;
     height: 0;
     border-bottom: 0.11rem solid #fff;
     border-right: 0.11rem solid #fff;
     border-left: 0.11rem solid #fff;
     border-top: 0.11rem solid #666;
     display: inline-block;
}
 /*分类filter*/
section{
     overflow: hidden;
     height: 7rem;
     width: 100%;
     font-size: 0.24rem;
     left: 0;
     right: 0;
     top: 100%;
     border-top: 1px solid #ddd;
     position: absolute;
     /*max-height: 0;*/
     background-color: #fff;
     -webkit-transition: all .2s ease-in-out;
     transition: all .2s ease-in-out;
     /*visibility: hidden;*/
     /*overflow: auto;*/
     opacity: 0;
     z-index: 3;

     /*opacity: 1;*/
     visibility: visible;
     max-height: 1000%;
}
 /*.scroll{*/
 /*position: absolute;*/
 /*display: flex;*/
 /*height: 100%;*/
 /*}*/
.open{
     opacity: 1;
     visibility: visible;
     max-height: 1000%;
}
section>ul:first-child li{
     padding: 0 .133333rem 0 .266667rem;
     overflow: auto;
}
section>ul:first-child>li.active{
     background-color: #fff;
}
section>ul:first-child{
     float: left;
     width: 40%;
     overflow: auto;
     height: 7rem;
}
 /*section>ul>li.active{*/
 /*background-color: #fff;*/
 /*}*/
section>ul:first-child>li{
     text-align: left;
     position: relative;
     line-height: 1.2rem;
}
.num{
     font-size: 0.15rem;
     /*position: absolute;*/
     margin-left: 0.9rem;
     line-height: .15rem;
     top: 50%;
     margin-top: -.186667rem;
     border-radius: .266667rem;
     border: .013333rem solid #eee;
     background-color: #fff;
     padding: 0 .1rem;
}
section>ul:first-child>li{
     background: #FAFAFA;
}
.shopfenlei{
     height: 7rem;
     overflow: auto;
     float: left;
     width: 54%;
     margin-left: .4rem;
     padding-right: .133333rem;
}
.shopfenlei img{
     margin-right: .2rem;
     width: .666667rem;
     height: .666667rem;
     vertical-align: middle;
}
.paixu{
     font-size: 0.24rem;
     left: 0;
     right: 0;
     top: 100%;
     border-top: 1px solid #ddd;
     position: absolute;
     /*max-height: 0;*/
     background-color: #fff;
     -webkit-transition: all .2s ease-in-out;
     transition: all .2s ease-in-out;
     /*visibility: hidden;*/
     overflow: auto;
     opacity: 0;
     z-index: 3;
}
.pxbox{
     height: 7rem;
     list-style: none;
     margin: 0;
     padding: 0;
}
.pxlist{
     position: relative;
     padding-left: .4rem;
     line-height: 1rem;
}
.pxlist:after{
     position: absolute;
     content: "";
     bottom: 0;
     left: 0.8rem;
     right: 0;
     height: 1px;
     background-color: #ddd;
}
.shaixuan{
     /*height: 7rem;*/
     left: 0;
     right: 0;
     top: 100%;
     border-top: 1px solid #ddd;
     position: absolute;
     /*max-height: 0;*/
     background-color: #fff;
     -webkit-transition: all .2s ease-in-out;
     transition: all .2s ease-in-out;
     /*visibility: hidden;*/
     overflow: auto;
     /*opacity: 0;*/
     z-index: 3;
     padding-bottom: 1.466667rem;
}
.sxbox{
     font-size: 0.24rem;
     overflow: auto;
     height: 100%;
     line-height: normal;
}
.sxbox dl{
     margin: .266667rem 0;
     padding: 0 .4rem;
     overflow: hidden;
}
.sxbox dt{
     margin-bottom: .2rem;
     color: #666;
}
.sxbox dd{
     margin: 0;
     float: left;
     width: 30%;
     margin-right: 2%;
     border: 1px solid #ddd;
     padding: .173333rem 0;
     height: .7rem;
     margin-bottom: .2rem;
     border-radius: .066667rem;
     -webkit-box-sizing: border-box;
             box-sizing: border-box;
}
.filterbtn{
     position: absolute;
     bottom: 0;
     left: 0;
     right: 0;
     border-top: 1px solid #eee;
     background-color: #fafafa;
     padding: 0 .133333rem;
     height: 1.4rem;
     display: -webkit-box;
     display: -ms-flexbox;
     display: flex;
     -webkit-box-align: center;
     -ms-flex-align: center;
     align-items: center;
}
.filterbtn a{
     font-size: .4rem;
     line-height: 1.093333rem;
     border-radius: .08rem;
     text-align: center;
     text-decoration: none;
     -webkit-box-flex: 1;
     -ms-flex: 1;
     flex: 1;
}
.filterbtn a:first-child{
     background-color: #fff;
     border: 1px solid #ddd;
     margin-right: .266667rem;
     color: #333;
}
.filterbtn a:last-child{
     color: #fff;
     background-color: #56d176;
     border: 1px solid #56d176;
}



