
































/*头*/
.header{
    position: relative;
    width: 100%;
    height:0.85rem;
    background: #FFD9B0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.arrow{
    width: 0.87rem;
    height: 0.87rem;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.arrow_left{
    font-size: 0.55rem;
    color: #fff;
    font-family: "宋体";
    font-weight: 800;
    text-align: center;
    line-height: 0.87rem;
}
.my{
    position: absolute;
    top:0;
    left:50%;
    font-weight: 300;
    font-size: 0.5rem;
    line-height: 0.87rem;
    color: #fff;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    font-family: "新宋体";
}


/*账户信息*/
.profile{
    padding: 0.4rem 0.4rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background: #FFD9B0;
    position: relative;
}
.profile_img{
    width: 1.6rem;
    height: 1.6rem;
    font-size: 1.6rem;
    line-height: 1.6rem;
    border-radius: 50%;
    background: #fff;
}
.profile_center{
    padding: 0.2rem 0;
    margin-left: 0.36rem;
}
.num{
    font-size: 0.47rem;
    margin-bottom: 0.2rem;
    font-weight: 400;
    color: #fff;
}
.pho{
    font-size: 0.31rem;
    color: #fff;
}
.arrow_right{
    position: absolute;
    font-family: "宋体";
    color: #fff;
    font-size: 0.4rem;
    font-weight: bold;
    right: 0.35rem;
    bottom:1.1rem
}



