
footer {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding-top: .12rem;
    background-color: #fff;
    height: 1.333333rem;
    -webkit-box-shadow: 0 -0.026667rem 0.053333rem rgba(0,0,0,.1);
            box-shadow: 0 -0.026667rem 0.053333rem rgba(0,0,0,.1);
}
.footer{
    width: 100%;
    height: 100%;
}
.footer>li{
    text-align: center;
    float: left;
    width: 25%;
    font-size: 0.25rem;
    height: 100%;
}
.footer>li>a{
    display: block;
    width: 100%;
    height: 100%;
    color: #999999;
}



